<script>
	export default {
		data() {
			return {
				hour: 1,
				rentIndex: 0,
				price: 1,
			}
		},
		computed:{
			amount: function(){
				return this.price * this.hour;
			}
		},
		methods: {
			inc(){
				this.hour = this.hour + 1
			},
			dec(){
				let hour = this.hour - 1
				hour = hour - 1 < 1? 1: hour
				this.hour = hour
			}
		}
	}
</script>

